#cy {
  background-color: #0000;
  background-image: radial-gradient(circle at 20% 30%, #6366f10f 0%, #0000 50%), radial-gradient(circle at 80% 70%, #8b5cf60f 0%, #0000 50%), radial-gradient(circle, #3b82f60a 0%, #0000 60%), radial-gradient(circle, #6366f114 1px, #0000 1px);
  background-position: 0 0, 0 0, 0 0, 0 0;
  background-repeat: repeat;
  background-size: 100% 100%, 100% 100%, 100% 100%, 24px 24px;
  background-attachment: scroll;
  background-origin: padding-box;
  background-clip: border-box;
  transition: background .3s;
  position: relative;
  overflow: hidden;
}

#cy:after {
  content: "";
  pointer-events: none;
  border-radius: inherit;
  position: absolute;
  inset: 0;
  box-shadow: inset 0 0 120px #6366f10d;
}

#cy:before {
  content: "";
  pointer-events: none;
  opacity: .6;
  will-change: transform;
  background: radial-gradient(circle, #6366f114 0%, #8b5cf60d 30%, #0000 60%);
  width: 200%;
  height: 200%;
  animation: 30s ease-in-out infinite aurora-float;
  position: absolute;
  top: -50%;
  left: -50%;
}

@keyframes aurora-float {
  0%, 100% {
    opacity: .4;
    transform: translate(0)rotate(0);
  }

  25% {
    opacity: .6;
    transform: translate(40px, -30px)rotate(5deg);
  }

  50% {
    opacity: .5;
    transform: translate(-20px, 40px)rotate(-3deg);
  }

  75% {
    opacity: .7;
    transform: translate(30px, 20px)rotate(4deg);
  }
}

#cy canvas {
  transition: filter .2s;
}

#cy:hover:before {
  animation-duration: 20s;
}

#cy.loading {
  background: linear-gradient(135deg, #f0f4f8 0%, #e2e8f0 100%);
  position: relative;
}

#cy.loading:after {
  content: "";
  z-index: 1000;
  border: 3px solid #6366f11a;
  border-top-color: #6366f1cc;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  margin: -30px 0 0 -30px;
  animation: 1s linear infinite spin;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

#cy.fullscreen {
  z-index: 10000 !important;
  border-radius: 0 !important;
  width: 100vw !important;
  height: 100vh !important;
  margin: 0 !important;
  position: fixed !important;
  inset: 0 !important;
}

#graphContainer #cy {
  border-radius: var(--radius-lg);
  box-shadow: inset 0 1px 3px #0000000a, 0 1px 2px #00000005;
}

#cy.empty:before {
  display: none;
}

#cy.empty {
  background: linear-gradient(135deg, #fafbfc 0%, #f0f4f8 100%);
  justify-content: center;
  align-items: center;
  display: flex;
}

.graph-empty-state {
  text-align: center;
  color: var(--text-secondary);
  pointer-events: none;
  z-index: 10;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.graph-empty-state-icon {
  margin-bottom: var(--space-lg);
  opacity: .3;
  background: var(--aurora-gradient-accent);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  font-size: 4rem;
}

.graph-empty-state h3 {
  margin-bottom: var(--space-sm);
  color: var(--text-primary);
  font-size: 1.5rem;
  font-weight: 600;
}

.graph-empty-state p {
  color: var(--text-secondary);
  max-width: 400px;
  font-size: 1rem;
}

@media (width <= 768px) {
  #cy {
    background-image: radial-gradient(circle, #6366f10d 1px, #0000 1px);
    background-size: 20px 20px;
  }

  #cy:before {
    opacity: .3;
    animation: none;
  }
}

@media (prefers-reduced-motion: reduce) {
  #cy:before {
    animation: none !important;
  }

  #cy.loading:after {
    animation: 2s linear infinite spin;
  }
}

@media (prefers-contrast: high) {
  #cy {
    background: #fff;
  }

  #cy:before, #cy:after {
    display: none;
  }
}

[data-theme="dark"] #cy {
  background-color: #0000;
  background-image: radial-gradient(circle at 20% 30%, #6366f11f 0%, #0000 50%), radial-gradient(circle at 80% 70%, #8b5cf61f 0%, #0000 50%), radial-gradient(circle, #ffffff08 1px, #0000 1px);
  background-position: 0 0;
  background-repeat: repeat;
  background-size: auto;
  background-attachment: scroll;
  background-origin: padding-box;
  background-clip: border-box;
}

[data-theme="dark"] #cy:before {
  background: radial-gradient(circle, #6366f126 0%, #8b5cf61a 30%, #0000 60%);
}

#cy {
  will-change: auto;
  transform: translateZ(0);
}

#cy:before {
  transform: translateZ(0);
}
